import React from 'react'
import PropTypes from 'prop-types'
// import { Link } from 'react-router-dom'
 
 

export default function Navbar(props) {
  return (
    

  <nav className={`navbar navbar-expand-lg    navbar-${props.mode==='light'?'light':'dark'}  bg-${props.mode}`}>
    <div className="container-fluid">
      <a className="navbar-brand" href="#">{props.navy}</a>
      {/* <Link className="navbar-brand" to="/">{props.navy}</Link> */}
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <a className="nav-link" herf="#">Home</a>
            {/* <Link className="nav-link" to="/">Home</Link> */}
          </li>
          <li className="nav-item">
            <a className="nav-link" herf="#">About</a>
            {/* <Link className="nav-link" to="/">About</Link> */}
          </li>
           
        </ul>
        {/* <form className="d-flex" role="search">
          <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
          <button className="btn btn-outline-success" type="submit">Search</button>
          
        </form> */}
       <div className={`form-check form-switch  text-${props.mode==='light'?'dark':'light'}`}>
        <input className="form-check-input" onClick={props.togglemode} type="checkbox" role="switch" id="flexSwitchCheckDefault"/>
        <label className="form-check-label "   htmlFor="flexSwitchCheckDefault">dark mode</label>
       </div>
      </div>
                
    </div>
  </nav>
 
  )
}


Navbar.prototype={
  navy:PropTypes.string.isRequired,
  home:PropTypes.string
}

Navbar.defaultProps= {
  navy:'simongoback'
}